import axios from 'axios'
let API_URL = ''

if (process.env.NODE_ENV === 'production') {
    API_URL = ''
} else {
    API_URL = 'http://localhost:3000'
}

export class APIService {
    constructor() {

    }

    getMalwares() {
        const url = `${API_URL}/api/malware/get`;
        return axios.get(url).then(response => response.data);
    }
}